.Landing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
  gap: 20px;
}
.Landing-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 1200px;
  padding: 10px 0;
  gap: 10px;
  background-color: var(--bg-color-1);
  color: var(--generalcontent-text);
  border-radius: 8px;
}
